




























import service from '../../../service.js';
export default {
	data() {
		return {
			caseList:[],
			head_img:'/static/missing-face.png'
		}
	},
	onLoad(option) {
		if(option.id){
			this.id = option.id
			
		}
	},
	mounted() {
		if(this.id){
			this.initData();
		}
	},
	methods: {
		initData(){
			let self = this;			
			uni.request({
				url:service.BASEURL+'/home/share/case_list',
				method:"POST",
				data:{
					company:this.id
				},
				success(res) {
					console.log(res);
					if(!res.data || res.data.code != 1){
							uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}else{
						if(!res.data || res.data.code != 1){
								uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}else{
							self.caseList = res.data.data.list;
						}
					}
				},
				complete() {
					setTimeout(function() {uni.hideLoading()}, 1500);
				}
			})
		},
		showCaseDetail(id){
			uni.navigateTo({
				url:`../casedetail/casedetail?id=${id}`
			})
		}
	}
}
